*{
  cursor: default;
}
#doubanResource{
  /* margin: 0 40px; */
  width: 400px;
  border: 1px solid #333;
  border-radius: 5px;
  background-color: #333;
  margin: 0 auto;
  font-size: 1em;
  margin-top: 10px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: "Microsoft YaHei";
}
#doubanResource ul{
  padding: 0;
  margin: 10px;
}
#doubanResource ul>li{
  list-style-type: none;
  background-color: #ddd;
  margin-top: 10px;
  cursor: default;
  padding: 5px;
  border-radius: 2px;
}
#doubanResource ul>li:hover{
  background-color: #eee;
  -webkit-box-shadow: 2px 2px 12px white;
          box-shadow: 2px 2px 12px white;
}
#doubanResource h2{
  text-align: center;
  color: #ccc;
}
#doubanResource h2:hover{
  color: #eee;
}
.movieMessage {
  color: black;
  background-color: #666;
  text-align: left;
  -webkit-box-shadow: -1px -1px 16px black inset;
          box-shadow: -1px -1px 16px black inset;
  padding: 15px;
}
.movieMessage h4{
  padding: 5px 10px;
  margin-top: 10px;
  margin-bottom: 0px;
}
.movieMessage li{
  list-style: none;
  padding: 2px;
  margin-left: 20px;
  display: inline-block;
}
.movieMessage li:hover{
  color: #ccc;
  background-color: #333;
}
.movieMessage * {
  /* display: inline-block; */
}
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
/* .bounce-leave-active {
  animation: bounce-in .5s reverse;
} */
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
